.sidebar{width:320px;line-height:1.3;margin-top:30px}.info-user{display:flex;align-items:center;margin:24px 0 20px}.info-avt{margin-right:16px;max-height:56px}.info-avt img{width:56px;height:56px;border-radius:50%;object-fit:cover}.info-content{flex:1}.info-content .info-name{color:var(--secondary-text)}.info-switch{font-size:1.2rem;font-weight:600;color:var(--primary-button-color);cursor:pointer}.suggestion-header{display:flex;justify-content:space-between}.suggestion-title{color:var(--secondary-text);font-weight:600}.suggestion-btn{font-size:1.2rem;font-weight:600}.suggestion-btn:hover{opacity:.5}.suggestion-content{padding:8px 0;margin-bottom:16px}.suggestion-friend{display:flex;align-items:center;padding:7px 0}.suggestion-friend .avt{margin-right:12px}.suggestion-friend .avt img{width:32px;height:32px;border-radius:50%;object-fit:cover}.suggestion-friend .content{flex:1}.suggestion-friend .content .name{color:var(--secondary-text)}.suggestion-friend .name{font-size:1.2rem}.suggestion-friend .follow{font-size:1.2rem;font-weight:600;color:var(--primary-button-color);cursor:pointer}.suggestion-friend .follow:hover{color:#00376b}.footer p{font-size:1.2rem;color:#c7c7c7}.nav-list{font-size:1.2rem;margin-bottom:20px}.link{display:inline-block;color:#c7c7c7;padding:1px 0}.link:not(:last-of-type)::after{content:"·";margin:0 3px}.link span:hover{text-decoration:underline}@media screen and (max-width: 1160px){.sidebar{width:100%}.info-user{display:none}.suggestion{display:none}.footer{display:flex;flex-direction:column;align-items:center;margin-bottom:60px}}